import React from "react";
import { Route } from "react-router";

import { Header } from "../header";
import { Navbar } from "../navbar";
import { Records } from "../records";
import { BookImage } from "../pages/book-image";
import { PhotoBook } from "../pages/photo-book";
import { Photography } from "../pages/photography";
import { PhotoImage } from "../pages/photo-image";

import "./app.sass";

export const App = () => {
    return (
        <div className="app">
            <Header />
            <div className="container app__container">
                <div>
                    <Navbar />
                </div>
                <div className="app__content">
                    <Route exact path="/" component={Records} />
                    <Route exact path="/photo-image" component={Records} />
                    <Route exact path="/book-image" component={Records} />
                    <Route exact path="/photography" component={Records} />
                    <Route exact path="/photo-book" component={Records} />
                    <Route path="/photo-image/:id" component={PhotoBook} />
                    <Route path="/book-image/:id" component={BookImage} />
                    <Route path="/photography/:id" component={Photography} />
                    <Route path="/photo-book/:id" component={PhotoImage} />
                </div>
            </div>
        </div>
    );
};
