import React from "react";
import { Link } from "react-router-dom";

import "./header.sass";

export const Header = () => {
    return (
        <header className="header__wrapper">
            <div className="header">
                <div className="header__logo">
                    <Link to="/">Администрирование</Link>
                </div>
            </div>
        </header>
    );
};
